import permision from "../js_sdk/wa-permission/permission.js"
const Utils = {
	// 
	async equipment() {
		// 
		if (uni.getSystemInfoSync().platform == 'ios') {
			//
			let oa = await Utils.requestAndroidPermission("camera", 'ios', '');
			//
			let ob = await Utils.requestAndroidPermission("record", 'ios', '');
			await uni.showToast({
				title: oa + ";" + ob,
				icon: "none",
				duration: 2000
			});
		} else if (uni.getSystemInfoSync().platform === 'android') {
			//
			let oa = await Utils.requestAndroidPermission("android.permission.CAMERA", 'android', '');
			//
			let ob = await Utils.requestAndroidPermission("android.permission.RECORD_AUDIO", 'android', '');
			await uni.showToast({
				title: oa + ";" + ob,
				icon: "none",
				duration: 2000
			});
		}
	},
	// 
	async requestAndroidPermission(permisionID, type, libe) {
		let result = 0;
		let strStatus = "";
		type == 'ios' ? result = await permision.judgeIosPermission(permisionID) : result = await permision
			.requestAndroidPermission(
				permisionID);
		if (result == 1) {
			strStatus = ","
		} else if (result == 0) {
			strStatus = ","
		} else {
			strStatus = ","
		};
		return libe + strStatus
	},
	// 
	getWidthHeight() {
		let obj = {
			windowWidth: 0,
			windowHeight: 0,
		}
		uni.getSystemInfo({
			success: function(res) {
				obj = Object.assign(obj, res);
			}
		})
		return obj
	}
}
export default Utils;
